import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Button from "react-bootstrap/Button";
import PingChart from "./components/ping_chart";
import DownloadChart from "./components/download_chart";
import Highcharts from 'highcharts';

const INTERVAL_MS = 200;
const PADDING_MS = 10000;
const DIRECTION = {
  IDLE: 'idle',
  DOWNLOAD: 'down',
  UPLOAD: 'up'
};

function wait(ms) {
  return new Promise(resolve => {
    setTimeout(() => {
      resolve()
    }, ms);
  })
}

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      started: false,
      paused: true,
      next_test: 0,
      download_measurements: {},
      data: [],
      latency_data: []
    };
    this.ping_timer = null;
    this.populate_tests();
    this.reset_abort_controller();
    console.log(Highcharts);
  }

  reset_abort_controller() {
    this.controller = new AbortController();
  }

  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            <Col lg={12}>
              <h1>The Khanifar Bufferbloat Test</h1>
              <p>Best buffer bloat test in the history of mankind, everâ„¢</p>
            </Col>
            <Col lg={12}>
              <PingChart data={this.state.latency_data}/>
            </Col>
            <Col lg={12}>
              <Button onClick={this.test_control.bind(this)}>{this.state.started ? (this.state.paused ? 'Continue' : 'Pause') : 'Start'}</Button> {' '}
              <Button onClick={this.restart_tests.bind(this)}>Restart Test</Button>
            </Col>
            <Col xs={12} md={6}>
              <span><strong>Download</strong></span>
            </Col>
            <Col xs={12} md={6}>
              <span><strong>Upload</strong></span>
            </Col>
            <Col xs={6} md={4}>
              <span><strong>latency:</strong> {this.state.latest_measurement}</span>
              <br/>
              <span><strong>bw:</strong> {this.state.latest_bw_measurement}Mbps</span>
            </Col>
          </Row>
          <Row>
            <Col xs={12} md={6}>
              <span><strong>Download Measurements</strong></span>
              {Object.keys(this.state.download_measurements).map(id => (
                <DownloadChart key={id}
                               data={this.state.download_measurements[id]}
                               color_id={id}
                               title={this.format_title(this.state.download_measurements[id])}
                />
              ))}
            </Col>
            <Col xs={12} md={6}>
              <span><strong>Upload Measurements</strong></span>
            </Col>
          </Row>
        </Container>
      </>
    );
  }

  format_title(measurements) {
    return `${measurements[0].title} Download: (${measurements.length}/${measurements[0].num_tries})`
  }

  async test_control() {
    if (!this.state.started) {
      await this.setState({
        started: true,
        paused: false
      });
    } else {
      await this.setState({
        paused: !this.state.paused
      });
    }
    if (this.state.paused) {
      this.controller.abort();
      this.reset_abort_controller();
      await wait(PADDING_MS);
      this.stop_ping();
    } else {
      if (this.state.next_test !== this.tests.length) {
        this.ping(INTERVAL_MS);
      }
      await wait(PADDING_MS);
      this.run_scenarios();
    }
  }

  stop_ping() {
    clearTimeout(this.ping_timer);
  }

  restart_tests() {
    this.setState({
      started: false,
      paused: true,
    });
  }

  populate_tests() {
    let id = 0;
    function _make_array(num_tries, direction, file_size, title) {
      id += 1;
      return new Array(num_tries).fill(0).map(r => {
        return {
          direction: direction,
          file_size: file_size,
          title: title,
          id: id,
          num_tries: num_tries
        };
      });
    }
    this.tests =
      [].concat(
        _make_array(10, DIRECTION.DOWNLOAD, ((1<<10) * 1000), '100KB') // 100KBs
      ).concat(
        _make_array(10, DIRECTION.DOWNLOAD, (1<<20), '1MB')
      ).concat(
        _make_array(10, DIRECTION.DOWNLOAD, (1<<20) * 10, '10MB')
      );
  }

  async run_scenarios() {
    if (this.state.next_test === this.tests.length) {
      console.log('Scenarios exhausted.', this.state.data);
      setTimeout(()=> {
        this.stop_ping();
      }, PADDING_MS);
      return;
    }
    let scenario = this.tests[this.state.next_test];
    this.state.data.push({...scenario});
    await this.setState({
      data: this.state.data
    });
    try {
      console.log(`Running scenario: ${this.state.next_test}`);
      await this.run_scenario(scenario);
      await this.setState({
        next_test: this.state.next_test + 1
      });
      this.run_scenarios();
    } catch (e) {
      console.log(e);
      this.state.data.pop();
      this.setState({
        data: this.state.data
      });
      console.log(`Scenario: ${this.state.next_test} was canceled.`);
    }
  }

  async run_scenario(scenario) {
    let url = `${App.BASE_URL}/${App.DOWN_ENDPOINT}`;
    let time = Date.now();
    let rate = await this.download(scenario.file_size, 1, url);
    this.collect_data(rate, time);
    console.log(`download was completed, rate: ${rate.toFixed(2)}Mbps`)
  }

  collect_data(rate, time) {
    this.state.data[this.state.next_test].rate = rate;
    this.state.data[this.state.next_test].time = time;
    this.setState({
      data: this.state.data
    });
    this.organize();
  }

  organize() {
    let last_item = this.state.data.slice(-1).pop();
    let key_name = `${last_item.id}`;
    if (last_item.direction === DIRECTION.DOWNLOAD) {
      let download_measurements = this.state.download_measurements;
      if (!(key_name in download_measurements)) {
        download_measurements[key_name] = [];
      }
      download_measurements[key_name].push(last_item);
    }
  }

  async download(bytes, streams, url) {
    let signal = this.controller.signal;
    let requests = Array(streams)
      .fill(0)
      .map(r => new Object())
      .map((req) => {
        req.start = Date.now();
        req.fetch = fetch(`${url}?bytes=${bytes}`, {signal});
        return req;
      });
    await Promise.all(requests.map(req => req.fetch.then(res => res.text().then(t => {
      req.finish = Date.now();
    }))));
    let rates = requests.map(r=>(bytes)/(r.finish - r.start));
    let total_rate = this.convert_to_mbps(rates.reduce((a, b) => a + b));
    this.setState({
      latest_bw_measurement: total_rate
    });
    this.log_rate(total_rate);
    return total_rate;
  }

  async record_latency(latency) {
    if (this.state.paused === this.state.next_test) {
      return;
    }
    let downloading = this.state.data.length !== this.state.next_test;
    this.capture_latency_plot_data(latency, downloading);
    this.capture_latency_with_download_measurement(latency, downloading);
    await this.sync_state_data();
  }

  capture_latency_plot_data(latency, downloading) {
    let id = null;
    // TODO: This whole id specification for series is very messy.
    if (downloading) {
      id = this.state.data[this.state.next_test].id;
    } else {
      if (this.state.next_test) {
        id = -1;
      } else {
        id = 0;
      }
    }
    this.state.latency_data.push({
      id: id,
      latency: latency
    });
  }

  capture_latency_with_download_measurement(latency, downloading) {
    if (!downloading) {
      return;
    }
    if (! ('latency' in this.state.data[this.state.next_test])) {
      this.state.data[this.state.next_test].latency = [];
    }
    this.state.data[this.state.next_test].latency.push(latency);
  }

  async sync_state_data() {
    await this.setState({
      latency_data: this.state.latency_data,
      data: this.state.data
    });
  }

  async ping(interval) {
    let start = Date.now();
    fetch("https://api.github.com/users/arshankhanifar")
      .then(res => res.json())
      .then(async res => {
        let end = Date.now();
        let latency = end - start;
        console.log('latency', latency);
        this.setState({
          latest_measurement: latency
        });
        await this.record_latency(latency);
      });
    this.ping_timer = setTimeout(() => this.ping(interval), interval);
  }

  componentDidMount() {
    this.setState({
      latest_measurement: 0,
      latest_bw_measurement: 0,
    });
  }

  static get CONFIG() {
    //return "arshan";
    return "cloudflare";
  }

  static get BASE_URL() {
    return (this.CONFIG === "arshan") ?
      "https://worker.arshankhanifar.workers.dev":
      "https://speed.cloudflare.com";
  }
  static get DOWN_ENDPOINT() {
    return (this.CONFIG === "arshan") ?
      "/hello/down":
      "__down";
  }

  convert_to_mbps(r) {
    return r*1000/(1<<20)*8;
  }

  log_rate(r) {
    return;
    console.log(`${this.convert_to_mbps(r).toFixed(2)}Mbps`);
  }
}
