import React, {Component} from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import Button from "react-bootstrap/Button";
import {DEFAULT_COLOURS} from "../utilities/colours";

export default class PingChart extends Component {
  chart_options = {
    chart: {
      zoomType: 'x',
      animation: {
        duration: 100
      }
    },
    title: {
      text: 'Latency'
    },
    subtitle: {
      text: document.ontouchstart === undefined ?
        'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in'
    },
    xAxis: {
      type: 'datetime'
    },
    yAxis: {
      title: {
        text: 'Ping(ms)'
      },
      gridLineWidth: 0,
    },
    legend: {
      enabled: false
    },
    plotOptions: {
      area: {
        marker: {
          radius: 2
        },
        lineWidth: 1,
        states: {
          hover: {
            lineWidth: 1
          }
        },
        threshold: null
      }
    },

    series: []
  };


  constructor(props) {
    super(props);
    this.chartComponent = React.createRef();
    this.total_points = 0;
    this.stopped = false;

  }

  render() {
    return (
      <>
        <HighchartsReact
          ref={this.chartComponent}
          highcharts={Highcharts}
          options={this.chart_options}/>
        <br/>

      </>
    );
    // add this for testing
    // <Button onClick={this.add_point.bind(this)}>Add Point</Button>
  }

  add_point() {
    const chart = this.chartComponent.current.chart;
    chart.series[0].addPoint([Date.now(), 0.75 + 0.25 * Math.random()]);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const chart = this.chartComponent.current.chart;
    if (!prevProps.data) {
      return;
    }
    if (this.total_points === prevProps.data.length) {
      return;
    }
    const last_point = prevProps.data.slice(-1).pop();
    if (!last_point) {
      return;
    }

    let measuring = last_point.id === 0 || last_point.id < 0;
    let time = Date.now();
    if (chart.series.length === last_point.id || last_point.id < 0 && !this.stopped) {
      this.add_the_same_point_to_last_series(time, last_point.latency);
      chart.addSeries({
        type: 'area',
        name: 'latency',
        data: [],
        color: measuring ? 'rgb(120,120,120)' : DEFAULT_COLOURS[last_point.id]
      });
      if (last_point.id < 0) {
        this.stopped = true;
      }
    }
    this.total_points += 1;
    console.log('last_point.id', last_point.id);
    chart.series[chart.series.length - 1].addPoint([time, last_point.latency]);
  }
  add_the_same_point_to_last_series(time, latency) {
    // just for prettiness
    const chart = this.chartComponent.current.chart;
    if (chart.series.length === 0) {
      return
    }
    chart.series[chart.series.length - 1].addPoint([time, latency]);
  }


  loadSampleData() {
    const chart = this.chartComponent.current.chart;
    fetch('https://cdn.jsdelivr.net/gh/highcharts/highcharts@v7.0.0/samples/data/usdeur.json')
      .then(res => res.json())
      .then(data => {
        this.chart.series[0].setData(data);
        this.chart.redraw();
      })
  }

  componentDidMount() {
  }
}
